.rmd-select:focus, .rmd-select.rmd-select__focus {
  outline: 0; }

.rmd-select--label {
  -webkit-transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1); }

.rmd-select--wrapper.rmd-menu-popup--wrapper__open .rmd-select--label,
.rmd-select--wrapper__active .rmd-select--label {
  color: #4CAF50; }

.rmd-select--wrapper.rmd-menu-popup--wrapper__open .rmd-select.rmd-select::after,
.rmd-select--wrapper__active .rmd-select.rmd-select::after {
  -webkit-transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1); }

.rmd-select--wrapper.rmd-menu-popup--wrapper__open .rmd-select--icon-down,
.rmd-select--wrapper__active .rmd-select--icon-down {
  fill: #4CAF50; }



