.rmd-form-input--error,
.rmd-form-input--warning {
  margin: 0.21429rem 0; }

.rmd-form-input--warning {
  color: rgba(255, 145, 0, 0.87); }

.rmd-form-input--error {
  color: rgba(255, 23, 68, 0.87); }

.rmd-input--wrapper__dark .rmd-form-input--warning,
.rmd-select--wrapper__dark .rmd-form-input--warning {
  color: #ff9100; }

.rmd-input--wrapper__dark .rmd-form-input--error,
.rmd-select--wrapper__dark .rmd-form-input--error {
  color: #ff1744; }



